*,*::before, *::after{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: #242428; 
}
.app {
  width: 100%; 
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  background-color: #242428;
}
/* -- Menu Button -- */
.nav-container{
  width: 100%; 
  background-color: transparent;
  padding: 15px 0;
  display: flex;
  z-index: 1003;
}
.button-container{
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  margin-left: 10px; 
  width: 40px;
  height: 40px;
  background-color: #333;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;  
}
.bi-list{
  font-size: 30px;
  color: white;
}
.input-container{
  position: relative;
  width: 200px;
  margin-left: 10px;
  z-index: 1001;
}
.inp-nav{
  width: 100%;
  padding: 10px 40px  10px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 15px;
}
.icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #ccc;
}
.inp-nav:focus + .icon {
  color: #000; 
}
.icon-container{
  display: flex;
  margin-left: 10px;
  z-index: 0;
}
.film-color{
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  margin-left: 10px;
  margin-top: 0px;
  font-size: 15px;
  letter-spacing: 5px;
}
.f-color{
  color: red;
}
.r-color{
  color: white;
}
.icon-size{
  width: 40px;
  height: 35px;
  object-fit: cover;
}
.menu {
  position: fixed;
  top: 0;
  left: -250px; 
  width: 250px;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  transition: left 0.3s ease;
  z-index: 10005;
  padding: 20px;
  box-sizing: border-box;
}
.menu-open {
  left: 0;
}

.menu h2 {
  margin-top: 0;
}

.menu ul {
  list-style-type: none;
  padding: 0;
}

.menu ul li {
  margin: 10px 0;
}

.menu ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 18px;
}

.menu ul li a:hover {
  text-decoration: underline;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .7); 
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  z-index: 10000;
}

.overlay-open {
  visibility: visible;
  opacity: 1;
  
}

.main-content {
  padding: 20px;
  width: 100%; 
  display: flex; 
}
.side-button{
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  height: 40px;
  width: 150px; 
  background: rgba(0, 0, 0, 0.6);
  border-radius: 30px;
  font-family: 'Courier New', Courier, monospace;
  z-index: 10010;
}
.bi-arrow-left{
  color: white;
  font-size: 20px;
}
.a-hover{
  margin-top: 10px;
  display: inline-block;
  font-size: 25px;
  border-bottom: 1px solid grey;
  text-decoration: none;
  color: white;
  width: 100%;
  height: 40px;
  font-family: 'Courier New', Courier, monospace;
}
.a-hover:first-of-type{
  height: 10px;
}
.a-hover:hover{
  color: red;
}
.genre-real{
  margin-top: 30px;
  font-size: 25px;
  font-family: 'Courier New', Courier, monospace;
}
.genre-div{
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 10px;
  
}
.genre-a{
  text-decoration: none;
  margin-top: 30px;
  font-family: 'Courier New', Courier, monospace;
  color: white;
}
.genre-a:hover{
  color: red;
}
.genre-a:first-of-type{
  margin-top: 10px;
}
.genre-a:nth-child(2){
  margin-top: 10px;
}
/* -- Menu Button -- */


/* -- Home Container -- */
.home-container {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  width: 100%;
  height: auto;
  z-index: 1;
}

.slideshow-container {
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; 
  height: 100%; 
  overflow: hidden; 
}

.slide {
  position: absolute;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  height: 100%;
  width: 100%;
  cursor: pointer;
}
.slide img {
  height: 100%;
  width: 100%;
  object-fit: fill;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0));
  -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0));
}
.slide.active {
  opacity: 1;
}

.movie-info {
  position: absolute;
  bottom: 150px;
  color: white;
  padding: 10px;
  border-radius: 5px;
  z-index: 10;
  font-family:Arial, Helvetica, sans-serif;
}
.movie-info h2 {
  margin: 0;
  width: 100px;
  color: white;
  font-size: 50px;
}
.movie-info p {
  color: white;
  font-size: 10px;
  width: 300px;
}
.navigation-buttons{
  position: absolute;
  bottom: 50px;
  width: 100%;
  margin-left: -20px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.prev-btn, .next-btn {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}
.bi-arrow-right{
  font-size: 20px;
}
.prev-btn:hover, .next-btn:hover {
  background-color: rgba(0, 0, 0, 0.8);
}
.movie-release , .movie-rating{
  margin-left: 10px;
}
.movie-review{
  margin-top: 20px;
  width: 715px;
  letter-spacing: 4px;
}
